import {React, useState}  from 'react'
import styled from 'styled-components'
import { addToCart } from './CartUtils';

export default function Card({props}){
    const [showEdit,setShowEdit] = useState(false);
    const [qty, setQty] = useState(props.id.quantity);

    const Box = styled.div`
        width: 80%;
        padding: 10px;
        margin-top: ${(window.innerWidth>600) ? "20px" : "5px"};
        margin-left:auto;
        margin-right:auto;
        border: black 1px solid;
        border-radius: ${(window.innerWidth>600) ? "20px" : "5px"};
    `;
    const ImagePC = styled.div`
        width: ${(window.innerWidth>600) ? "25%" : "90%"};
        background: url(${props.id.img}) no-repeat;
        background-size: contain;
        border-radius: ${(window.innerWidth>600) ? "20px" : "5px"};
    `;
    const ImageMobile = styled.div`
        width: ${(window.innerWidth>600) ? "25%" : "90%"};
        height: 150px;
        background: url(${props.id.img}) no-repeat;
        background-size: contain;
        border-radius: ${(window.innerWidth>600) ? "20px" : "5px"};
    `;
    const Text = styled.div`
        font-weight: bold;
        color: red;
    `;
    const Cost = styled.div`
        width: 100%;
        text-align: right;
        margin-top:${(window.innerWidth>600) ? "20px" : "5px"};
    `;
    const Edit = styled.div`
        width: 24px;
        height: 24px;
        margin-left: ${(window.innerWidth>600) ? "20px" : "5px"};
        margin-right:${(window.innerWidth>600) ? "20px" : "5px"};
        margin-top: ${(window.innerWidth>600) ? "20px" : "5px"};
        background: url(${process.env.PUBLIC_URL + '/edit.png'});
        cursor: pointer;
    `;
    const Remove = styled.div`
        width: 24px;
        height: 24px;
        margin-left: ${(window.innerWidth>600) ? "20px" : "5px"};
        margin-right:${(window.innerWidth>600) ? "20px" : "5px"};
        margin-top: ${(window.innerWidth>600) ? "20px" : "5px"};
        background: url(${process.env.PUBLIC_URL + '/delete.png'});
        cursor: pointer;
    `;
    const FlexBox = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: space-between;
    `;
    const FlexBoxE = styled.div`
        width:100%;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: flex-end;
    `;
    const BackBox = styled.div`
        background: rgba(0,0,0,0.5);
        width: 100%;
        height: 100%;
        position: fixed;
        top:0;
        left:0;
    `;
    const EditBox = styled.div`
        margin: auto;
        margin-top:${(window.innerWidth>600) ? "250px" : "350px"};
        padding: 5px 5px 10px 10px;
        border-radius: 5px;
        width: fit-content;
        height: min-content;
        background:white;
    `;
    const Add = styled.div`
        background: url(${process.env.PUBLIC_URL + '/add.png'});
        border: black 1px solid;
        width:24px;
        height:24px;
    `;
    const Minus = styled.div`
        background: url(${process.env.PUBLIC_URL + '/remove.png'});
        border: black 1px solid;
        width:24px;
        height:24px;
    `;
    const CloseButton = styled.div`
        width:fit-content;
        margin: 0px 0 5px auto;
        cursor: pointer;
        width:18px;
        height:18px;
        background: url(${process.env.PUBLIC_URL + '/close.png'});
    `;
    const QtyBox = styled.div`
        width: min-content;
        padding: 1px 10px;
        border-top: black 1px solid;
        border-bottom: black 1px solid;
    `;
    const Save = styled.div`
        background: lime;
        padding: 5px 10px;
        margin: 5px auto;
        cursor: pointer;
        border-radius: 5px;
        width:fit-content;
    `;

    const add = () => (setQty(qty+1));
    const minus = () =>{
        if(qty>1){
            setQty(qty-1);
        }
    };
    const close = () =>{
        setQty(props.id.quantity);
        setShowEdit(false);
    };
    const save = () => {
        props.edit(props.id,qty-props.id.quantity);
        setShowEdit(false);
    }


    return(<>
        <Box>
            <FlexBox>
                {(window.innerWidth>600) ? <ImagePC></ImagePC> : <ImageMobile></ImageMobile>}
                <div style={{width:(window.innerWidth>600) ? "70%" : "100%"}}>
                    <Text>{props.id.name}</Text>
                    <Cost>Cost: {props.id.quantity} * {props.id.cost} = <b>${props.id.quantity*parseFloat( props.id.cost.substring(2))}</b></Cost>
                    <FlexBoxE>
                        <Edit onClick={() => setShowEdit(true)} />
                        <Remove onClick={() => props.remove(props.id)}/>
                    </FlexBoxE>
                </div>
            </FlexBox>
        </Box>
        {(showEdit) ? 
        <BackBox>
            <EditBox>
                <CloseButton onClick={close} />
                <FlexBox style={{marginRight:"5px"}}>
                    <div style={{marginRight:"10px"}}>Qty: </div>
                    <Minus onClick={minus}/>
                    <QtyBox><b>{qty}</b></QtyBox>
                    <Add onClick={add}/>
                </FlexBox>
                <Save onClick={save}>Save</Save>
            </EditBox>
        </BackBox>
        : <></>}
            

    </>);
}