import { React, useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import styled from 'styled-components'
import "../../Assets/YUI3.css"



export default function Headers() {
    const [showMenu, setShowMenu] = useState(false);


    const FlexBoxS = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: space-between;
    `;

    const FlexBox = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: space-between;
    `;
    const TopBox = styled.div`
        position: absolute;
        top: 0;
        left: 0;
        width:100%;
    `;

    const Header = styled.div`
        background: black;
        border-bottom: red 2px solid;
        color: white;
        font-weight: bold;
        font-size:1.25em;
        padding-top:5px;
        padding-bottom: 5px;
    `;

    const Navigation = styled.div`
        padding: 10px 20px;

    `;

    const LeftHeader = styled.div`
        margin-left:${(window.innerWidth>600) ? "5%" : "10px"};
    `;

    const RightHeader = styled.div`
        width: fit-content;
    `;

    const RightHeaderUnit = styled.div`
        margin-right: ${(window.innerWidth>600) ? "20px" : "10px"};
    `;
    const MenuItem = styled.div`
        padding: 10px;
        border-top: red solid 2px;
    `;
    const SocialMedia = styled.div`
        background: black;
        border-bottom: solid 2px red;
        border-left: ${(window.innerWidth>600) ? "solid 2px red" : "solid 0px red"};
        width: ${(window.innerWidth>600) ? "200px" : "94.5%"};
        border-bottom-left-radius: ${(window.innerWidth>600) ? "100px" : "0px"};
        margin-left: ${(window.innerWidth>600) ? "auto" : "0px"};
        padding-left: ${(window.innerWidth>600) ? "100px" : "10px"};
        padding-right: ${(window.innerWidth>600) ? "20px" : "10px"};
        padding-top: ${(window.innerWidth>600) ? "10px" : "5px"};
        padding-bottom: ${(window.innerWidth>600) ? "10px" : "5px"};
    `;
    const onMenuClick= () => (setShowMenu(!showMenu));

    
    

    return(
        <>
        <TopBox>
        <Header>
            <FlexBoxS>
                <LeftHeader>
                    <FlexBoxS>
                        <img src={process.env.PUBLIC_URL + '/HeaderBanner400.png'} width={(window.innerWidth>600) ? "400px" : "50%" } height="50px" alt="LeftHeader"/>
                        {!(window.innerWidth>600) ? <>
                            <img src={process.env.PUBLIC_URL + '/menu.png'} style={{marginRight:"20px"}} onClick={()=>onMenuClick()} /> 
                            <Link to="/cart"><img src={process.env.PUBLIC_URL + '/Cart.png'} style={{marginRight:"20px",marginTop:"12.5px",cursor:"pointer"}} /></Link>
                        </>: <></>}
                    </FlexBoxS>
                </LeftHeader>
                <RightHeader>
                {window.innerWidth>600 ? <>
                    <FlexBox>
                    <RightHeaderUnit><Navigation><Link to="/about">About</Link></Navigation></RightHeaderUnit>
                    <RightHeaderUnit><Navigation><Link to="/all-products">All Products</Link></Navigation></RightHeaderUnit>
                    <RightHeaderUnit><Navigation><Link to="/category">All Categories</Link></Navigation></RightHeaderUnit>
                    <RightHeaderUnit><Link to="/cart"><img src={process.env.PUBLIC_URL + '/Cart.png'} style={{marginRight:"20px",marginTop:"12.5px",cursor:"pointer"}} /></Link></RightHeaderUnit>
                    </FlexBox>
                </> : <></>}
                </RightHeader>
            </FlexBoxS>
            
            { showMenu? <>
            <MenuItem onClick={()=>onMenuClick()}><Link to="/about">About</Link></MenuItem>
            <MenuItem onClick={()=>onMenuClick()}><Link to="/all-products">All Products</Link></MenuItem>
            <MenuItem onClick={()=>onMenuClick()}><Link to="/category">All Category</Link></MenuItem>
            </> : <></>}
        </Header>
        <SocialMedia>
            <FlexBox>
            <RightHeaderUnit><img src={process.env.PUBLIC_URL + '/FacebookLogo35x35.png'} width="25px" height="25px" /></RightHeaderUnit>
            <RightHeaderUnit><img src={process.env.PUBLIC_URL + '/InstagramLogo35x35.png'} width="25px" height="25px"/></RightHeaderUnit>
            <RightHeaderUnit><img src={process.env.PUBLIC_URL + '/LinkedinLogo35x35.png'} width="25px" height="25px"/></RightHeaderUnit>
            <RightHeaderUnit><img src={process.env.PUBLIC_URL + '/TwitterLogo35x35.png'} width="25px" height="25px"/></RightHeaderUnit>
            </FlexBox>
        </SocialMedia>
        </TopBox>
        <div style={{height:(window.innerWidth>600) ? "40px" : "100px"}}></div>
        </>
    );
}