var cart = localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : [];
const saveCart = () => (localStorage.setItem("MyCart",JSON.stringify(cart)));
const addToCart = (el,qty) => {
    let flag = 0;
    cart.map(function(item){
        if (item.id === el.id){
            flag= 1;
            item.quantity+=qty;
        }
    })
    if (flag === 0){
        const dict = el;
        dict.quantity = qty;
        cart.push(dict);
    }
    saveCart();
    console.log(JSON.parse(localStorage.getItem("MyCart")));
};
const removeFromCart = (el) => {
    let hardCopy = [...cart];
    hardCopy = hardCopy.filter((cartItem) => cartItem.id !== el.id);
    cart = hardCopy;
    saveCart();
    console.log(JSON.parse(localStorage.getItem("MyCart")));
};

export {addToCart, removeFromCart, cart}