import {React, useState} from 'react'
import styled from 'styled-components'
import {addToCart} from '../Cart/CartUtils'


export default function({props}){
    const [qtyText, setQtyText] = useState(1);

    const onTextChange = (e) => {
        setQtyText(e.target.value)
    };
    const addQty = () => ((setQtyText(qtyText+1)));
    const minusQty = () => {
        if (qtyText>1){
            setQtyText(qtyText-1);
        }
    }

    const Backside = styled.div`
        position: fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background: rgba(0,0,0,0.5);
    `;
    const Containeer = styled.div`
        background: rgb(255,255,255);
        padding: ${(window.innerWidth>600) ? "50px" : "20px 10px"};
        border: black 1px solid;
        boder-radius: 10px;
        width: ${(window.innerWidth>600) ? "75%" : "90%"};
        max-height: ${(window.innerWidth>600) ? "75%" : "90%"};
        margin: ${(window.innerWidth>600) ? "2%" : "10px"} auto ${(window.innerWidth>600) ? "2%" : "10px"} auto;
    `;
    const CloseButton = styled.div`
        width:fit-content;
        margin-left:auto;
        cursor: pointer;
    `;
    const FlexBox = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: space-evenly;
    `;
    const Image = styled.div`
        width: ${(window.innerWidth>600) ? "45%" : "90%"};
        height: ${(window.innerWidth>600) ? "80%" : "25%"};
        margin-top: ${(window.innerWidth>600) ? "0px" : "10px"};
    `;
    const DetailBox = styled.div`
        width: ${(window.innerWidth>600) ? "45%" : "90%"};
        height: ${(window.innerWidth>600) ? "80%" : "70%"};
    `;
    const Name = styled.div`
        text-align:center;
        font-size: ${(window.innerWidth>600) ? "1.5em" : "1.5em"};
        width: 100%;
        margin-bottom: ${(window.innerWidth>600) ? "20px" : "10px"};
        font-weight: bold;
    `;
    const LeftAlignText = styled.div`
        font-size: ${(window.innerWidth>600) ? "1.25em" : "1.5em"};
        width: 100%;
        margin-bottom: ${(window.innerWidth>600) ? "20px" : "10px"};
    `;
    const QtyBox = styled.div`
        width: min-content;
        margin-left:auto;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        align-items: center;

        -webkit-flex-flow: nowrap;
        flex-wrap: nowrap;
        justify-content: flex-end;
    `;
    const Button = styled.div`
        padding-left: 0.75em;
        padding-right: 0.75em;
        width: fit-content;
        height: 1.5em;
        margin-top: 1em;
        border: black 2px solid;
    `;

    const onClickNone = () => (console.log());

    return(
    <>
    <Backside>
        <Containeer onClick={()=>onClickNone()}>
            <CloseButton>
                <img src={process.env.PUBLIC_URL + '/close.png'} onClick={()=>props.onClose()} />
            </CloseButton>
            <FlexBox>
                <Image>
                    <div style={{margin:"auto"}}>
                    <img src={props.product.img} width= {(window.innerWidth>600) ? "80%" : "25%"} height={(window.innerWidth>600) ? "80%" : "25%"} />
                    </div>
                </Image>
                <DetailBox>
                    <Name>{props.product.name}</Name>
                    <LeftAlignText style={{fontWeight:"bold"}}>Product ID: {props.product.id} </LeftAlignText>
                    <LeftAlignText>{props.product.desc}</LeftAlignText>
                    <QtyBox>
                        <LeftAlignText style={{fontWeight:"bold", textAlign:"center", lineHeight:"100%", verticalAlign:"100%"}}>Qty: </LeftAlignText>
                        <div><input type="number" onChange={onTextChange} value={qtyText} /></div>
                    </QtyBox>
                    <FlexBox>
                        <Button onClick={() => addToCart(props.product,parseInt(qtyText))}>Add to card</Button>
                    </FlexBox>
                </DetailBox>
            </FlexBox>
        </Containeer>
    </Backside>
    </>);
}