import {React, useState, useEffect} from 'react'
import styled from 'styled-components'
import {addToCart, removeFromCart} from "./CartUtils"
import Card from './ProductCard';
import Skeleton from '../Products/Skeleton';
import ProCard from '../Products/Product'

export default function Checkout({props}){
    const [cart, setCart] = useState(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : []);
    const [total, setTotal] = useState(calculateTotal());
    const [products, setProducts] = useState([]);
    const [loaded, setLoaded] = useState(false);
    const t1= [];
    for (let i=0; i<5 ;i++){
        t1.push(i);
    }

    const removeElem = (elem) =>{
        removeFromCart(elem);
        setCart(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : [])
        setTotal(calculateTotal());
    };
    const edit = (elem,qty) => {
        addToCart(elem,qty);
        setCart(localStorage.getItem("MyCart") ? JSON.parse(localStorage.getItem("MyCart")) : []);
        const value = calculateTotal()
        setTotal(value);
    }
    function calculateTotal(){
        var sum = 0;
        for (let i=0; i<cart.length; i++){
            sum += cart[i].quantity*parseFloat(cart[i].cost.substring(2));
        }
        return (sum);
    }
    const getProducts = async() => {
        await fetch('https://fakestoreapi.com/products?limit=5')
        .then(res=>res.json())
        .then(json=>setProducts(json))
        setLoaded(true);
    }

    useEffect(() => {
        getProducts();
        return () => {};
    }, []);


    const Heading = styled.div`
        padding: 20px;
        padding-top: ${(window.innerWidth>600) ? "50px" : "20px"};
        padding-bottom: ${(window.innerWidth>600) ? "50px" : "20px"};
        font-size: ${(window.innerWidth>600) ? "10em" : "4em"};
        font-weight: bold;
        text-align: center;
        background: url(https://png.pngtree.com/thumb_back/fh260/back_our/20190619/ourmid/pngtree-shopping-mall-supermarket-selection-merchandise-poster-background-material-image_133225.jpg);
        background-size: cover;
        background-position: center center;
    `; 
    const SubHeading = styled.div`
        padding-left: ${(window.innerWidth>600) ? "100px" : "20px"};
        padding-top: ${(window.innerWidth>600) ? "50px" : "20px"};
        padding-bottom: ${(window.innerWidth>600) ? "50px" : "20px"};
        font-size: ${(window.innerWidth>600) ? "2em" : "1.5em"};
        font-weight: bold;
        text-align: center;
    `;
    const Para = styled.div`
        padding-left: ${(window.innerWidth>600) ? "100px" : "20px"};
        padding-top: ${(window.innerWidth>600) ? "50px" : "20px"};
        padding-bottom: ${(window.innerWidth>600) ? "50px" : "20px"};
        font-size: ${(window.innerWidth>600) ? "1.5em" : "1em"};
        font-weight: bold;
        width: fit-content;
    `;
    const Box1 = styled.div`
        width: ${(window.innerWidth>600) ? "60%": "95%"};
        margin: 20px auto;
        padding: 10px 20px;
    `;
    const Box2 = styled.div`
        width: ${(window.innerWidth>600) ? "30%": "95%"};
        margin: 20px auto;
        padding: 10px 20px;
    `;
    const FlexBox = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: space-evenly;
    `;
    const FlexBoxE = styled.div`
        margin-left:0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;

        -webkit-flex-flow: row wrap;
        flex-wrap: wrap;
        justify-content: flex-end;
    `;


    return(<>
        <Heading>My Cart</Heading>
        <FlexBox>
            <Box1>{(cart.length>0) ? <>
                {cart.map((id,index) => <>
                    <Card  props={{"id":id,"remove":removeElem, "edit":edit}} key={index} />
                </>)}
                <FlexBoxE>
                <Para style={{marginRight:"25px"}}>Total Cost: </Para>
                <Para><b>$ {total}</b></Para>
                </FlexBoxE>
                </>: <><SubHeading>No item in cart.</SubHeading></>}
            </Box1>
            <Box2>
                <SubHeading>Other Products</SubHeading>
                { (!loaded) ? <>
                    {console.log(products)}
                    {t1.map((id,index) => (
                        <Skeleton key={id}/>
                    ))} 
                    </>: <>{products.map((id,index) => (
                        <ProCard key={index} props={{"name":id.title,"img":id.image,"id":id.id,"desc":id.description}} />
                    ))} </> }
            </Box2>
        </FlexBox>
        
        
    </>);
}