import {React, useState} from 'react'
import styled from 'styled-components'

export default function SnackBar({props}){
    const [show,setShow] = useState(props.show);
    const Box = styled.div`
        width: ${(window.innerWidth>600) ? "400px" : "100%"};
        background: #fdde6c;
        border: black solid 1px;
        position: fixed;
        left: ${(window.innerWidth>600) ? "20px" : "0"};
        bottom: ${(window.innerWidth>600) ? "20px" : "0"};
    `;
    const Close = styled.div`
        position: relative;
        top: ${(window.innerWidth>600) ? "10px" : "5px"};
        right: ${(window.innerWidth>600) ? "10px" : "5px"};
        width: fit-content; 
    `;
    const TextBox = styled.div`
        margin-right: ${(window.innerWidth>600) ? "10px" : "5px"};
        margin-top: ${(window.innerWidth>600) ? "10px" : "5px"}
        margin-left: ${(window.innerWidth>600) ? "10px" : "5px"};
    `;

    return (<>
        {show? 
        <Box>
        <Close><img src={process.env.PUBLIC_URL + '/close.png'} onClick={()=>setShow(false)} /></Close>
        <TextBox>{props.text}</TextBox>
        </Box> : <></>}
        
    </>);
}